$spacer: 1rem ;
$spacers: (
  
  0:0,
  1:($spacer * 0.25), //4
  2: ($spacer * 0.5), //8
  3:  ($spacer * 0.75) ,//12 
  4:$spacer,
  5: ($spacer * 1.25) ,//20
  6: ($spacer * 1.5), //24
  7: ($spacer * 1.75), //28
  8: ($spacer * 2), //,32
  9: ($spacer * 2.25), //36
  10: ($spacer * 2.5), //40 
  11: ($spacer * 3), //48
  14: ($spacer * 3.5), //56
  15: ($spacer * 3.75), //60,
  16: ($spacer * 4),
  17: ($spacer * 4.5), //72,
  18: ($spacer * 9), //120 
    'banner': 88px, 
);

$grid-gutter-width: 1rem;
$gutters: (
  0: 0,
  1: ($grid-gutter-width * .25),
  2:  ($grid-gutter-width *.5),
  3: $grid-gutter-width,
  4: ($grid-gutter-width * 1.5),
  5:  ($grid-gutter-width *3),
);


$headings-margin-bottom:0;
$font-family-sans-serif:'Noto Sans TC', sans-serif;
$paragraph-margin-bottom:0;
$font-size-base: 1rem;
$font-size: () !default;
$font-size: map-merge(
  (
    'xs': ($font-size-base *0.75),
    'sm': ($font-size-base *0.875),
    'base': $font-size-base,
    'lg': ($font-size-base * 1.125),
    'xl' :($font-size-base * 1.25),
    '2xl': ($font-size-base * 1.5),
    '3xl': ($font-size-base * 1.75),
    '4xl': ($font-size-base * 2.25),
    
  ),
  $font-size
);


// $secondary:var(--bs-secondary)!default;
$border-color:var(--bs-primary) !default;

