.list__group--dark.list__group--border{
        .list__item{
            border-bottom:1px solid rgba(0,0,0,0.2);
            
            
        }
        
    }
.list__group--dark {
    .list__title{
        border-color:#121212;
    }
}
.list__group--border{
    .list__item{
        border-bottom:1px solid rgba(255,255,255,0.2);
        
        
    }
    
}
.list__group--center{
    text-align: center;
    p{ 
            text-align: center !important; 
    }
}
.list__group--equal{
    p{
        width: 50% !important;
    }
}
.list__title{
    line-height: 1.5;
    padding-bottom: 8px;
    border-bottom:1.5px solid #fff;
}
.list__item{
    line-height: 1.2;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{ 
       
        word-break: break-all;
        &:nth-child(2){
            width: 30%;
            text-align: right;
        }
    }
}
.list__text{
    width: 70%;
    flex-shrink: 0;
    padding-right: 10px;
}
.list__text--extra{
    font-size: 14px;
    opacity: 0.8;
    display: block;
}

.list--column{
    .list__item{
        flex-direction: column;
        align-items: start;
        @include media-breakpoint-up(sm) {
            flex-direction: row;
            align-items: center;
         }
        p{
            width: 100% !important;
            text-align: left;
            &:first-child{
                margin-bottom: 8px;
            }
            @include media-breakpoint-up(sm) {
                &:first-child{
                    width: 70% !important;
                    margin-bottom: 0px;
                }
                &:nth-child(2){
                    width: 30% !important;
                    text-align: right;
                }
             }
        }
    }
}
.list__group--equal.list--column{
    .list__item{
      
        p{
            
            @include media-breakpoint-up(sm) {
              width: 50% !important;
             }
        }
    }
}