

.tab__menuLink{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: 400;   
    padding: 10px 15px;
    border-radius: 4px;
    line-height: 1.5;
    height: 100%
    
}
.tab__content{
    position: relative;
}

.tab__back{
    position: absolute;
    width: 100%;
    height: 100%;
    top:-24px;
    left: 0;
    display: none;
    text-align: right;
   
    @include media-breakpoint-up(md) {
        display: block;
       }
}
.tab__backBtn{
    background-color: #fff;
    line-height: 1.2;
    color: #000;
    border-radius: 4px;
    box-shadow:2px 2px 5px rgba(0,0,0,0.5);
    position: sticky;
    top: 400px;
    padding: 5px;
    display: inline-block;
    z-index: 5;
    opacity: 0;
    transition: all 0.5s;
    &:hover{
        opacity: 1 !important;
        color: #000;
    }
    span{
        display: block;
        text-align: center;
        animation:  opacity 6s infinite ;
    }
}
.tab-pane{
    display: none;
    
}
.tab-pane.active{
    display: block;
}

.tab__menu,.tab__menu--light{
    .tab__menuLink{
        border: 1px solid #fff;
        color:#fff;
        &:hover{
            color:#121212;
            background-color: #fff;
        }   
    }
    .tab__menuLink--active{
        color:#121212;
        background-color: #fff;
    }
}
.tab__menu--dark{
    .tab__menuLink{
        border: 1px solid #121212;
        color:#121212;
        &:hover{
            color:#fff;
            background-color: #121212;
        }   
    }
    .tab__menuLink--active{
        color:#fff;
        background-color: #121212;
    }
}
.tab__dropdown{
    .tab__menu{
        display: none;
        @include media-breakpoint-up(md) {
            display: flex !important;
           }
    }
}
.tab__dropdownBtn{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
    padding: 10px 15px;
    border-radius: 10px;
    line-height: 1.5;
    background-color: #fff;
    font-weight: 500;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.3);
    color:#121212;
    &::after{
        border-width: 0.4em;
    }
    @include media-breakpoint-up(md) {
        display: none;
       }
}
.tab__dropdownBtn--dark{
    background-color: #242424;
    color:#242424;
}
.tab__menu--dropdown{
    width: 100%;
    margin: 0 auto !important;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.3);
    li{
       padding: 0;
        width: 100%;
    }
    .tab__menuLink{
        justify-content: start;
        border: 0;
        border-radius: 0;     
    }
}


.tab__menu--dropdown,.tab__menu--light.tab__menu--dropdown{
   
    background-color: #fff;
   
    .tab__menuLink{
        color:#121212;
        &:hover{
            color:#121212;
            background-color: #cecece;
        }   
    }
    .tab__menuLink--active{
        color:#121212;
        background-color: #cecece;
    }  
}
.tab__menu--dark.tab__menu--dropdown{
   
    background-color: #242424;
  
    .tab__menuLink{
        color:#fff;
        &:hover{
            color:#fff;
            background-color: #3a3a3a;
        }   
    }
    .tab__menuLink--active{
        color:#fff;
        background-color: #3a3a3a;
    }  
}
@keyframes opacity{
    0%{
        opacity: 0.3;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0.3;
    }
}