.collapse__title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom:2px solid #fff;
    span{
        font-size: 30px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.5s;
        &:hover{
            opacity: 0.6;
        }
    }
}
.collapse__img{
    margin-bottom: 8px;
}
.collapse__content{
    display: none;
    padding: 12px;
    border-bottom:2px solid #fff;
    @include media-breakpoint-up(md) {
        padding: 20px;
     }
}