.hover__bg--light{
    transition: all 0.5s;
    &:hover{
        background:#fff;
        color:#121212;
    }
}
.hover__bg--dark{
    transition: all 0.5s;
    &:hover{
        background:#121212;
        color:#fff;
    }
}
.hover__text--light{
    transition: all 0.5s;
    &:hover{
       
        color:#fff;
    }
}
.hover__text--dark{
    transition: all 0.5s;
    &:hover{
       
        color:#121212;
    }
}

.hover__bg--primary{
    transition: all 0.5s;
    &:hover{
        background:var(--bs-primary);
        color:#fff;
    }
}
.hover__bg--secondary{
    transition: all 0.5s;
    &:hover{
      background:var(--bs-secondary);
        color:#fff;
    }
}
.hover__text--primary{
  transition: all 0.5s;
  &:hover{
  
      color:var(--bs-primary);
  }
}
.hover__text--secondary{
  transition: all 0.5s;
  &:hover{
      color:var(--bs-secondary);
  }
}
